:root {
  --mainColor1: #202124;
  --mainColor2: #212b31;
  --mainColor3: #424c55;
  --fontColor: #fff;
  --main-background-color: #212b31;
  --colorHover: #ce5f5d;
  --lineColor: whitesmoke;
  --otherColor1: rgb(24, 75, 185);
  --otherColor2: rgb(60, 221, 11);
}

@font-face {
  font-family: webFont;
  src: url(./UI/SansationLight.woff);
}

.theme-dark {
  --mainColor1: #232B32;
  --mainColor2: #152028;
  --mainColor3: #202945;
  --fontColor: whitesmoke;
  --main-background-color: #f9f9f9;
  --colorHover: lightseagreen;
} 

body {
  /* background-color: var(--main-background-color); */
  font-family: webFont;
}


.center {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.main-content {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  color: var(--fontColor);
  min-height: 84vh;
  /* background: var(--mainColor3); */
  /* padding: 20px 0 20px 0; */
}

.App {
  min-height: 100vh;
  background-color: var(--main-background-color);
}


header {
  font-size: 35px;
}

header, footer, header .center, footer .center {
  position: relative;
  background-color: var(--mainColor1);
  padding: 0px;
  text-align: center;
  color: var(--fontColor);
  min-height: 8vh;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}

header .center {
  flex-direction: column;
}


header .site-title {
  margin: auto;
}

header .horizontal-line {
  padding: 0 15px 0 15px;
}

header .horizontal-line .line {
    min-height: 10px;
    width: 100%;
    border-top: 1px solid var(--lineColor);
}

footer {
  flex-direction: row;
}

footer label {
  margin: auto 0 auto 0;
}

footer label p {
  padding-bottom: 1vh;
}

.component-selection, .component-display {
  padding: 15px;
}

.component-selection {
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  align-items: center;
  width: 30%;
  /*background: var(--mainColor2);*/
}
.component-selection .container, .component-display .container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  align-items: center;
  overflow: auto;
}

/*
.component-selection .container {
  background: var(--mainColor2);
}
*/

.component-selection .container, .component-display .container {
  background: var(--mainColor3);
}

.component-selection nav {
  width: 90%;
  height: 90%;
}

.component-selection nav ul {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-size: large;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.component-selection li {
  height: 100%;
  margin: 10px;
}

.component-selection a {
  text-decoration: none;
}

.component-selection .navigation-link {
  display: flex;
  align-items: center;
  background: var(--mainColor2);
  border-radius: 10px;
  color: var(--fontColor);
  width: 100%;
  height: 100%;
  transition: all 0.5s;
}

.component-selection .navigation-link:hover {
  padding-left: 10%;
  width: 90%;
  background: var(--colorHover);
  /* color: var(--mainColor2); */
}

.component-selection .navigation-link p {
  padding-left: 10px;
}

.component-selection .navigation-link-height {
  min-height: 40px;
}


.component-display {
  width: 70%;
  /*background: var(--mainColor3);*/
  overflow: auto;
}

.GuessTheNumber {
  background-color: var(--mainColor2);
  border-radius: 10%;
  padding: 5vh;
  margin: 8vh;
}

.GuessTheNumbers {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.Tweets {
  min-height: 60vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.tweet {
  /* border: 2px solid black; */
  height: 300px;
  min-width: 300px;
  background-color: white;
  border-radius: 20%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 1vh;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.center {
  transition: max-width 0.5s;
}

@media (min-width: 576px) {
  .center {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .center {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .center {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .center {
     max-width: 1140px;
  }
}

@media (min-width: 1380px) {
  .center {
     max-width: 1250px;
  }
}

@media (max-width: 768px) {
  .main-content {
    flex-direction: column;
  }
  .component-selection, .component-display, header .horizontal-line {
    width: 100%;
    padding-right: 0;
    padding-left: 0;
  }

  /*
  .component-display {
    min-height: 70vh;
  }
  */
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


