#tic-tac-toe {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    min-height: 70vh;
    padding: 5px;
}

#tic-tac-toe .button-new-game {
    margin-top: 5px;
    max-width: 400px;
    min-width: 400px;
    text-align: center;
    background-color: var(--mainColor2);
    border-radius: 5px;
    padding: 1vh 0vh 1vh 0vh;
    cursor: pointer;
}

#tic-tac-toe .button-new-game:hover {
    background-color: var(--mainColor1);
}

#tic-tac-toe .turn-indicator {
    display: grid;
    grid-template-columns: 7fr 3fr;
    margin-bottom: 5px;
    max-width: 400px;
    min-width: 400px;
    font-size: x-large;
    
}

#tic-tac-toe .turn-indicator-p1 {
    grid-template-areas: 'text .';
    grid-template-columns: 7fr 3fr;
}

#tic-tac-toe .turn-indicator-p1 div {
    background-color: var(--otherColor1);
}

#tic-tac-toe .turn-indicator-p2 {
    grid-template-areas: '. text';
    grid-template-columns: 3fr 7fr;
}

#tic-tac-toe .turn-indicator-p2 div {
    background-color: var(--otherColor2);
}

#tic-tac-toe .turn-indicator-draw {
    grid-template-areas: '. text .';
    grid-template-columns: 2fr 6fr 2fr;
}

#tic-tac-toe .turn-indicator-draw div {
    background-image: linear-gradient(to bottom right, var(--otherColor1), var(--otherColor2));
}

#tic-tac-toe .turn-indicator div {
    border-radius: 10px;
    grid-area: text;
    text-align: center;
}

#tic-tac-toe .board {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 5px;
    max-width: 400px;
    min-width: 400px;
}

#tic-tac-toe .board div {
    aspect-ratio: 1/1;

    background-color: var(--mainColor2);
    color: var(--fontColor);
    display: flex;
    justify-content: center;
    align-items: center;
}

#tic-tac-toe .board .cell-p1-selected {
    background-color: var(--otherColor1);
}

#tic-tac-toe .board .cell-p2-selected {
    background-color: var(--otherColor2);
}

#tic-tac-toe .board .cell-unselected-p1-turn:hover {
    cursor: pointer;
    background-color: var(--otherColor1);
}

#tic-tac-toe .board .cell-unselected-p2-turn:hover {
    cursor: pointer;
    background-color: var(--otherColor2);
}