#table-exercise .save-input, #table-exercise .cancel-input {
    display: inline-block;
    padding: 8px;
    margin-top: 8px;
}

#table-exercise .cell-edit-field {
    display: block;
    padding: 8px;
}

#table-exercise tbody tr:nth-child(odd) {
    background-color: var(--mainColor1);
}

#table-exercise thead th {
    background-color: var(--mainColor2);
}

#table-exercise th .header-content {
    display: flex;
    flex-direction: row;
}

#table-exercise th .header-content .title-center {
    min-width: 80%;
    margin: auto;
}

#table-exercise th .header-content .sort-icon {
    width: 100%;
    max-height: 30px;
}

#table-exercise th, #table-exercise td {
    border: 1px solid var(--mainColor2);
    text-align: center;
    padding: 8px;
    min-width: 80px;
}

/*
#table-exercise svg {
    max-height: 25px;
    position: absolute;
    right: 1px;
    top: 1px;
}
*/

#table-exercise .notes li {
    list-style: inside;
    list-style-type: none;
}

#table-exercise table, #table-exercise form, #table-exercise .notes {
    padding: 20px 20px 0px 20px;
}

#table-exercise td, #table-exercise th {
    cursor: pointer;
}

